            <svg t="1666150596093" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="6661" width="200" height="200"><path d="M864 64H160C107.072 64 64 107.072 64 160v608c0 52.928 43.072 96 96 96h61.92v123.232L443.84 864H864c52.928 0 96-43.072 96-96V160c0-52.928-43.072-96-96-96z m32 704a32 32 0 0 1-32 32H443.84c-10.912 0-21.664 2.816-31.072 8.064l-126.88 70.4V864c0-35.296-28.704-64-64-64H160c-17.632 0-32-14.336-32-32V160c0-17.632 14.368-32 32-32h704c17.664 0 32 14.368 32 32v608z" p-id="6662"></path><path d="M640 520H256a32 32 0 1 0 0 64h384a32 32 0 1 0 0-64zM768 296H256a32 32 0 0 0 0 64h512a32 32 0 1 0 0-64z" p-id="6663"></path></svg>
          </div>
          <div>
            <span v-if="!msg.is_fav" @click="collection">
              <svg t="1666150543681" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="5658" width="200" height="200"><path d="M284.458667 941.397333c-36.437333 15.637333-68.48-7.68-64.896-47.168l22.613333-248.917333-164.394667-188.053333c-26.069333-29.824-13.653333-67.562667 24.789334-76.309334l243.370666-55.381333 127.786667-214.677333c20.288-34.090667 59.946667-34.069333 80.213333 0l127.786667 214.677333 243.370667 55.381333c38.656 8.789333 50.858667 46.485333 24.789333 76.309334l-164.394667 188.053333 22.741334 249.002667c3.605333 39.509333-28.458667 62.805333-64.896 47.146666l-229.504-98.517333-229.376 98.453333z" fill="#2c2c2c" p-id="5659"></path></svg>
            </span>
            <span v-else @click="cancelcollection">
              <svg t="1666752828918" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="2547" width="200" height="200"><path d="M284.458667 941.397333c-36.437333 15.637333-68.48-7.68-64.896-47.168l22.613333-248.917333-164.394667-188.053333c-26.069333-29.824-13.653333-67.562667 24.789334-76.309334l243.370666-55.381333 127.786667-214.677333c20.288-34.090667 59.946667-34.069333 80.213333 0l127.786667 214.677333 243.370667 55.381333c38.656 8.789333 50.858667 46.485333 24.789333 76.309334l-164.394667 188.053333 22.741334 249.002667c3.605333 39.509333-28.458667 62.805333-64.896 47.146666l-229.504-98.517333-229.376 98.453333z" fill="#d81e06" p-id="2548"></path></svg>
            </span>
          </div>
          <div>
            <span v-if="!msg.is_like" @click="zan">
              <svg t="1666752663616" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="2571" width="200" height="200"><path d="M64 483.04V872c0 37.216 30.144 67.36 67.36 67.36H192V416.32l-60.64-0.64A67.36 67.36 0 0 0 64 483.04zM857.28 344.992l-267.808 1.696c12.576-44.256 18.944-83.584 18.944-118.208 0-78.56-68.832-155.488-137.568-145.504-60.608 8.8-67.264 61.184-67.264 126.816v59.264c0 76.064-63.84 140.864-137.856 148L256 416.96v522.4h527.552a102.72 102.72 0 0 0 100.928-83.584l73.728-388.96a102.72 102.72 0 0 0-100.928-121.824z" p-id="2572" fill="#2c2c2c"></path></svg>
            </span>
            <span v-else @click="cancelzan">
              <svg t="1666150497310" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="4688" width="200" height="200"><path d="M710.549333 384.810667a12409.045333 12409.045333 0 0 0 47.466667-0.32l8.746667-0.085334c83.989333-0.618667 141.44 67.584 126.72 150.229334L847.296 794.026667c-10.026667 56.448-63.914667 101.546667-121.130667 101.589333L298.624 896a42.730667 42.730667 0 0 1-42.666667-42.410667l-0.810666-383.978666a42.666667 42.666667 0 0 1 42.026666-42.666667l3.157334-0.064c5.226667-0.042667 11.797333-0.042667 19.626666 0 91.946667 0.768 170.88-86.698667 170.709334-170.944-0.149333-86.741333 39.786667-126.762667 106.453333-127.573333 62.250667-0.746667 106.602667 59.605333 107.349333 149.12 0.213333 26.602667-6.293333 73.237333-14.506666 107.434666 6.186667 0 13.077333-0.042667 20.586666-0.085333z m-497.706666 63.232L213.333333 874.624A21.312 21.312 0 0 1 191.786667 896H149.525333A21.333333 21.333333 0 0 1 128 874.624l0.042667-426.581333A21.269333 21.269333 0 0 1 149.44 426.666667h41.984c11.669333 0 21.418667 9.578667 21.418667 21.376z" fill="#d81e06" p-id="4689"></path></svg>
            </span>
          </div>
          <div>
            <svg t="1666150741793" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="7601" width="200" height="200"><path d="M565.934 817.574a34.816 34.816 0 0 0 9.818 29.394l0.302 0.241a35.539 35.539 0 0 0 25.6 10.963c11.143 0 20.66-5.42 27.226-13.312l354.545-387.072a35.117 35.117 0 0 0 10.24-27.106 35.057 35.057 0 0 0-10.24-27.106L626.892 14.336a36.503 36.503 0 0 0-51.2 0 34.936 34.936 0 0 0-9.758 29.395V253.35c-295.996 0-535.974 238.893-535.974 533.684a529.468 529.468 0 0 0 44.454 212.51C116.7 777.276 329.812 608.437 565.332 608.437l0.602 209.137z" p-id="7602"></path></svg>
          </div>
        </div>
      </div>
      <van-popup v-model="show" position="bottom" :style="{ height: '30%' }" >
        <textarea class="wenben" style="word-break:break-all" v-model="pinglun" placeholder="请输入内容"></textarea>
        <div class="fabu"><van-button type="primary" @click="fbpinglun">发布</van-button></div>
      </van-popup>
  </div>
</template>

<script>
import { Popup,Button, Toast } from 'vant';
import axios from 'axios'
export default {
  name:'lcontent',
  components:{
    [Popup.name]:Popup,
    [Button.name]:Button
  },
data() {
    return {
        pinglun:null,
        show:false,
        showshow:false,
        showpl:false,
        id:'',
        msg:{author:'',title:'',username:'',imageSrc:[],time:'',content:''},
        comments:[],
        isShow:true,
        idid:'',
        aId:'',
        hfpl:'',
        pllist:[],
    }
},
methods:{
  diclick(){
    // console.log(111);
    if(localStorage.getItem('uid') === null) {
      Toast('请先登录，再进行操作!')
    }else{
      this.show = !this.show
    }
  },
  ontime(time){
      // console.log(time);
      let timestamp = new Date();
      // console.log(timestamp);
      let timer = (timestamp-time) / 1000;
      //如果小于5分钟,则返回”刚刚”,其他以此类推
      let tips='';
      switch (true) {
        case timer < 300:
            tips='刚刚';
            break;
        case timer >= 300 && timer<3600:
            tips=parseInt(timer/60)+'分钟前';
            break;
        case timer >= 3600 && timer<86400:
            tips=parseInt(timer/3600)+'小时前';
            break;
        case timer >= 86400 && timer<2592000:
            tips=parseInt(timer/86400)+'天前';
            break;
        case timer >= 2592000 && timer< (86400*365):
            tips=parseInt(timer/(86400*30))+'个月前';
            break;
        default:
            tips=parseInt(timer/(86400*365))+'年前';
            break;
      }
      return tips
    },
  huigupl(ididid){
    this.aId = ididid
    this.showshow = !this.showshow;
  },
  plpl(ididid,val){
    this.showshow = false;
    this.idid = ididid
    val.reply_num === 0 ? this.idid='':null
    // 获取回复评论的列表
    axios({
      method:'post',
      url:'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/get_reply_list',
      data:{
        "skip": 0, //起始条数
        "limit": 10, //一次返回的条数
        "user_id": localStorage.getItem('uid'), //用户ID
        "reply_comment_id": ididid, //回复的评论ID comment_type为1时有效  comment_type为0时可以不填
        "article_id": this.id //文章ID
      }
    }).then(res =>{
      // console.log(res);
        this.pllist = res.data.data
    })
  },
  // 点赞评论
  plzan(id){
    axios({
      method:'post',
      url:'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/comment_like',
      data:{
        user_id: localStorage.getItem('uid'), //用户ID
        comment_id: id //点击的那条评论的ID
      }
    }).then(res =>{
      // console.log(res.data.count);
      this.zan = res.data.count
      this.incontent()
    })
  },
  // 取消点赞评论
  cancleplzan(id){
    axios({
      method:'post',
      url:'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/comment_unlike',
      data:{
        user_id: localStorage.getItem('uid'), //用户ID
        comment_id: id //点击的那条评论的ID
      }
    }).then(res =>{
      // console.log(res.data.count);
      this.zan = res.data.count
      this.incontent()
    })
  },
  fbhfpl(){
    
    // 回复评论
     axios({
      method:'post',
      url:'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/add_comment',
      data:{
        "user_id": localStorage.getItem('uid'), //用户ID
        "article_id":this.id , //文章ID
        "content": this.hfpl, //内容 
        "reply_comment_id":this.aId , //回复的评论ID comment_type为1时有效  comment_type为0时可以不填
        "comment_type": 1 //回复类型，0表示回复文章，1表示回复评论
      }
      }).then(res =>{
        // console.log(res);
        // console.log(this.id );
        // console.log(this.hfpl);
        // console.log(this.aId);
        // console.log(localStorage.getItem('uid'));
        this.incontent()
        this.plpl()
        this.showshow = !this.showshow
        this.hfpl = ''
      })
  },
  fbpinglun(){
    // 对文章发布评论
    axios({
      method:'post',
      url:'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/add_comment',
      data:{
        "user_id": localStorage.getItem('uid'), //用户ID
        "article_id":this.id , //文章ID
        "content": this.pinglun, //内容 
        // "reply_comment_id":'' , //回复的评论ID comment_type为1时有效  comment_type为0时可以不填
        "comment_type": 0 //回复类型，0表示回复文章，1表示回复评论
      }
    }).then(res =>{
      this.show = false
      this.pinglun = ''
      this.incontent()
    })
  },
  // 获取文章详情
  incontent(){
    // console.log(this.$route.params.id);
    this.id = this.$route.params.id
    // console.log(this.id);
    axios({
        method:'post',
        url:'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/get_article_detail',
        data: {
            article_id:this.id,
            user_id:localStorage.getItem('uid')
        },
    }).then(res =>{
        // console.log(res.data.data);
        this.msg = res.data.data
        // console.log(this.msg);
    }),
    // 获取文章全部评论
    axios({
        method:'post',
        url:'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/get_comment_list',
        data: {
            article_id:this.id,
            user_id:localStorage.getItem('uid')
        },
    }).then(res =>{
        // console.log(res.data.data);
        this.comments = res.data.data
        // this.num = res.data.data.reply_num
        // console.log(this.comments);
    })
  },
  // 收藏文章
  collection(){
    if(localStorage.getItem('uid') === null) {
      Toast('请先登录，再进行操作!')
    }else{
      axios({
      method:'post',
      url:'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/add_fav',
      data:{
        user_id: localStorage.getItem('uid'), //用户ID
        article_id: this.id //文章ID
      }
    }).then(res =>{
      // console.log(res);
      Toast('收藏成功')
      this.incontent()
    })
    }
  },
//  取消收藏
  cancelcollection(){
    axios({
      method:'post',
      url:'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/remove_fav',
      data:{
        user_id: localStorage.getItem('uid'), //用户ID
        article_id: this.id //文章ID
      }
    }).then(res =>{
      // console.log(res);
      this.incontent()
    })
  },
  // 点赞文章
  zan(){
    if(localStorage.getItem('uid') === null) {
      Toast('请先登录，再进行操作!')
    }else{
      axios({
      method:'post',
      url:'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/like',
      data:{
        user_id: localStorage.getItem('uid'), //用户ID
        article_id: this.id //文章ID
      }
    }).then(res =>{
      // console.log(res);
      Toast('点赞成功')
      this.incontent()
    })
    }
  },
  // 取消点赞
  cancelzan(){
    axios({
      method:'post',
      url:'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/unlike',
      data:{
        user_id: localStorage.getItem('uid'), //用户ID
        article_id: this.id //文章ID
      }
    }).then(res =>{
      // console.log(res);
      this.incontent()
    })
  },
  back(){
    this.$router.go(-1)
  },
  changeshow(){
    this.isShow = !this.isShow
    if(!this.isShow){
      Toast('关注成功')
    }
  }
},
activated(){
// console.log(2);
},
created(){
  this.incontent()
},
}
</script>

